// import { useState, useEffect } from 'react'
import { ListProps, ListsGroupProps } from '@/interfaces'

export const List = (props: ListProps) => {
  const { store, active, label, time, className } = props
  const click = () => {
    if (store?.id === active?.id) return
    props.setState(props.store || {})
  }
  return (
    <div className={`eca-list-item ${className || ''}`} onClick={click}>
      <div>{label}</div>
      <div>{time}</div>
    </div>
  )
}

export const ListsGroup = function (props: ListsGroupProps) {
  const { options, active, isActiveClass, setState } = props
  return (
    <div className="eca-list-group">
      {options.length
        ? options.map((v, i) => {
            return (
              <List
                key={i}
                {...v}
                idx={i}
                store={v}
                active={active}
                className={`${active?.id === v?.id ? isActiveClass || 'is-active' : ''}`}
                setState={setState}
              />
            )
          })
        : '暂无数据'}
    </div>
  )
}
