interface LayoutProps {
  Left?: React.ReactNode
  Center?: React.ReactNode
  Right?: React.ReactNode
  className?: string
  style?: any
}
export default function (props: LayoutProps) {
  const { Left, Center, Right, className } = props
  return (
    <div className={`eca-layout-room ${className}`}>
      {Left && <div className="eca-layout-left">{Left}</div>}
      <div className="eca-layout-content">
        {Center && <div className="eca-layout-center">{Center}</div>}
        {Right && <div className="eca-layout-right">{Right}</div>}
      </div>
    </div>
  )
}
