import Space from '../Space'
interface TabItem {
  label: string
  types: string
}
interface TabsProps {
  cur: string
  setcur: any
  tabs: TabItem[]
}

export const Tabs = (props: TabsProps) => {
  const { cur, setcur, tabs } = props

  return (
    <div className="eca-tabs-content">
      <Space>
        {tabs.map((v, i) => {
          return (
            <div
              key={i}
              className={`eca-tabs-item ${cur === v.types ? 'is-active-tab' : ''}`}
              onClick={() => setcur(v.types)}
            >
              {v.label}
            </div>
          )
        })}
      </Space>
    </div>
  )
}
