import { useState, useEffect } from 'react'
import { dataSourceTemParse, formatReceiveDataSource } from '@/utils'
import { changeStatusOrder, handlers } from '@/chrome'
import { ListItemProps, QueryResponse, UseStateUI, CurTypes } from '@/interfaces'

export const useStateUI = (): UseStateUI => {
  const defCur = 'rules'
  const defCurMenu = (): ListItemProps => ({
    id: '',
    label: '',
    time: '',
    store: {
      rules: {
        nodes: [],
        links: [],
      },
      effect: {
        pre: null,
        next: null,
      },
      dictionary: {},
    },
  })
  const tabsUIData = [
    {
      label: '规则执行',
      types: 'rules',
    },
    { label: '数据影响', types: 'effect' },
    { label: '规则循环 (2)', types: 'cycle' },
  ]
  const [cur, setCur] = useState(defCur)
  const [menu, setMenu] = useState([])
  const [curMenu, setCurMenu] = useState(defCurMenu())
  const [loading, setLoading] = useState(false)
  return {
    defCur,
    tabsUIData,
    cur: cur as CurTypes,
    setCur,
    menu,
    setMenu,
    loading,
    setLoading,
    curMenu,
    setCurMenu,
    defCurMenu,
  }
}

export const useControl = (props: UseStateUI) => {
  const setCurMenu = (source: any) => {
    props.setCurMenu(source)
    props.setCur(props.defCur)
  }
  const receiveUpdateSource = (dataSource: QueryResponse[]) => {
    const parseSource = dataSourceTemParse(dataSource)
    props.setCur(props.defCur)
    props.setMenu(parseSource)
    props.setCurMenu(parseSource.length ? parseSource[0] : props.defCurMenu())
  }
  const start = () => changeStatusOrder(true)
  const stop = () => changeStatusOrder(false)
  const upload = () => 'upload'
  const downLoad = () => 'downLoad'
  const receiveDataSourceParser = (str: string) => {
    console.log('🚀 ~ file: useControl.tsx ~ line 73 ~ receiveDataSourceParser ~ str', str)
    receiveUpdateSource(formatReceiveDataSource(str))
  }
  useEffect(() => {
    // 订阅接受消息机制
    handlers.noticePanel.push(receiveDataSourceParser)
    return () => {
      handlers.noticePanel.pop()
    }
  })

  return {
    receiveUpdateSource,
    setCurMenu,
    start,
    stop,
    upload,
    downLoad,
  }
}

