function App() {
  const stateUI = useStateUI()
  const control = useControl(stateUI)
  // useImperativeHandle(ref, () => ({
  //   control,
  // }))
  return (
    <div className="App">
      <HeaderUtil control={control} getData={getMock} />
      {(stateUI.menu && stateUI.menu.length > 0 && (
        <Layout
          className="app-layout"
          Left={ListsGroup({
            options: stateUI.menu,
            active: stateUI.curMenu,
            setState: control.setCurMenu,
          })}
          Center={View({ stateUI, control })}
        />
      )) ||
        '暂无数据'}
    </div>
  )
}

export default App
