import {
  QueryResponse,
  ParseTemData,
  Dictionary,
  ResolveEffectTem,
  ParseTemDataRulesNodes,
  ParseTemDataRulesLink,
} from '@/interfaces'
export const getTemData = (item: QueryResponse): ParseTemData => {
  return {
    id: item.batchId,
    label: item.trigger.title,
    time: item.trigger.time,
    store: {
      rules: {
        nodes: item.fields.map((v) => ({
          name: v.field,
          store: v?.detail,
        })),
        links: item.rules.map((v) => ({
          source: v.dependent,
          target: v.target,
          value: v.weight,
          effect: v.effect,
        })),
      },
      effect: {
        pre: item.state.oldValue,
        next: item.state.newValue,
      },
      dictionary: item.ruleDefs,
    },
  }
}
export const dataSourceTemParse = (source: QueryResponse[]) => source.map((v) => getTemData(v))

export const formatReceiveDataSource = (str: string): QueryResponse[] => {
  try {
    return JSON.parse(str)
  } catch (error) {
    return []
  }
}

export const resolveEffectConfig = (
  origin: ParseTemDataRulesLink,
  dictionary: Dictionary,
): ResolveEffectTem[] => {
  return origin.effect.map((v) => {
    const d = dictionary[v.ruleDefId]
    return {
      id: v.ruleDefId,
      label: d.name,
      store: {
        variables: v.variables,
        params: d.params,
      },
    }
  })
}

export const setNodesLinks = (nodes: ParseTemDataRulesNodes[], links: ParseTemDataRulesLink[]) => {
  return {
    title: {
      text: '汉语文字 aaaa',
    },
    tooltip: {
      trigger: 'item',
    },
    series: {
      type: 'sankey',
      draggable: false,
      data: nodes,
      links: links,
      dataZoom: {
        start: 0,
        end: 200,
      },
      emphasis: {
        focus: 'adjacency',
      },
      lineStyle: {
        color: 'source',
        curveness: 0.5,
      },
    },
  }
}

export const getDom = (id: string) => document.getElementById(id) as HTMLElement

export const formatJsonStr = (source: any, space = 2) => {
  try {
    return JSON.stringify(source, null, space)
  } catch (error) {
    return JSON.stringify({ error: `${source} failed praser to string` }, null, space)
  }
}

export const syntaxHighlight = (source: Record<string, any> | string) => {
  let json = source
  if (typeof source !== 'string') {
    json = JSON.stringify(source, undefined, 2)
  } else {
  }
  json = json.replace(/&/g, '&amp;').replace(/</g, '&lt;').replace(/>/g, '&gt;')
  return json.replace(
    /("(\\u[a-zA-Z0-9]{4}|\\[^u]|[^\\"])*"(\s*:)?|\b(true|false|null)\b|-?\d+(?:\.\d*)?(?:[eE][+\-]?\d+)?)/g,
    function (match: string) {
      var cls = 'number'
      if (/^"/.test(match)) {
        if (/:$/.test(match)) {
          cls = 'pre-key'
        } else {
          cls = 'pre-string'
        }
      } else if (/true|false/.test(match)) {
        cls = 'pre-boolean'
      } else if (/null/.test(match)) {
        cls = 'pre-null'
      }
      return '<span class="' + cls + '">' + match + '</span>'
    },
  )
}
