import { dsMock1, dsMock2, dsMock3 } from '@/mock'
const BASE_PATH = 'window.__EAC_DEV_TOOLS__'
let count = 1

export const getMock = () => {
  if (count > 3) count = 1
  if (count === 1) {
    ++count
    return dsMock1()
  }
  if (count === 2) {
    ++count
    return dsMock2()
  }
  if (count === 3) {
    ++count
    return dsMock3()
  }
}
export const checkIsInDevtools = () => window.chrome && !!window.chrome.devtools

export const handlers: Record<string, Function[]> = {
  noticePanel: [],
}
window.contentScriptReceiver = (data: any) => {
  console.log('🚀 ~ file: app.js ~ line 49 ~ data panel.html', data)
  if (data.source && data.source === '__EAC_DEV_TODOS__') {
    const handler = handlers.noticePanel

    if (handler && handler.length) {
      handler.forEach((v) => {
        if (typeof v === 'function') {
          v(data.data)
        }
      })
    }
  }
}

export const noticeContentWindow = (methodAndArgs: string) => {
  if (checkIsInDevtools()) {
    window.chrome.devtools.inspectedWindow.eval(`${BASE_PATH}.${methodAndArgs}`)
  }
}

export const changeStatusOrder = (t: boolean) => {
  if (checkIsInDevtools()) {
    noticeContentWindow(`action(${t})`)
  } else {
  }
}
