import Layout from '@/components/Layout'
import { ListsGroup } from '@/components/List'
import EchoJSON from './EchoJSON'
import { ResolveEffectTem } from '@/interfaces'

interface ListJsonProps {
  style?: any
  options: ResolveEffectTem[]
  active: ResolveEffectTem
  setState: Function
}

export default function (props: ListJsonProps) {
  console.log('🚀 ~ file: ListJSON.tsx ~ line 15 ~ props', props)
  const { options, active, setState } = props
  const { variables, params } = active.store

  return (
    <Layout
      Left={ListsGroup({
        options: options,
        setState: setState,
        active: active,
        isActiveClass: 'is-active-list',
      })}
      Center={EchoJSON({ title: '规则定义', json: params })}
      Right={EchoJSON({ title: '表达式的值', json: variables })}
    />
  )
}
