import React, { useEffect, useState } from 'react'
// import React from 'react'
import * as echarts from 'echarts'
import ListJSON from './ListJSON'
import { RuleDefination, ResolveEffectTem } from '@/interfaces'
import { resolveEffectConfig, setNodesLinks, getDom } from '@/utils'
interface ChartPanelProps {
  es: any
  dictionary: Record<string, RuleDefination>
}

export const ChartPanel = (props: ChartPanelProps) => {
  const { es, dictionary } = props
  const [state, setState] = useState({
    options: [] as ResolveEffectTem[],
    active: {} as ResolveEffectTem,
  })
  const id = 'eca-panel-chart'
  const updateActive = (active: ResolveEffectTem) => {
    setState({
      active,
      options: state.options,
    })
  }
  useEffect(() => {
    let myChart = echarts.getInstanceByDom(getDom(id))
    if (myChart === undefined) {
      myChart = echarts.init(getDom(id))
      myChart.on('click', (e: any) => {
        if (!e.data.source) return
        let { data } = e
        const res = resolveEffectConfig(data, dictionary)
        console.log('🚀 ~ file: ChartPanel.tsx ~ line 146 ~ myChart.on ~ res', res)
        const len = res.length
        setState({
          active: res[0] || {},
          options: len ? res : [],
        })
      })
      myChart.resize()
      myChart.setOption(setNodesLinks(es.nodes, es.links))
    }
  })

  return (
    <div>
      <div id={id} style={{ width: '100%', height: '300px' }}>
        chart
      </div>
      <div className="line" style={{ marginBottom: '5px' }} />
      {!!state.options?.length && (
        <ListJSON options={state.options} active={state.active} setState={updateActive} />
      )}
    </div>
  )
}

