import CodeDiff from '@/components/DiffCode'
import { formatJsonStr } from '@/utils'
// import { left1, right1 } from './diffJsonMock'

export default function (props: { pre: Record<string, any>; next: Record<string, any> }) {
  const { pre, next } = props
  return (
    <div className="box-content diff-json-container">
      <CodeDiff
        oldStr={formatJsonStr(pre)}
        newStr={formatJsonStr(next)}
        context={10}
        theme="light"
        outputFormat="side-by-side"
      />
    </div>
  )
}
