import { Tabs } from '@/components/Tabs'
import { ChartPanel } from './ChartPanel'
import DiffJSON from './DiffJSON'
import { UseStateUI } from '@/interfaces'

interface PanelProps {
  stateUI: UseStateUI
  control: any
}
export default function (props: PanelProps) {
  const { stateUI } = props
  const { rules, effect } = stateUI.curMenu.store
  const DispatchContent = () => {
    if (stateUI.cur === 'rules' || stateUI.cur === 'cycle') {
      return <ChartPanel es={rules} dictionary={stateUI.curMenu?.store?.dictionary} />
    }
    if (stateUI.cur === 'effect') return <DiffJSON pre={effect.pre} next={effect.next} />
    return null
  }
  return (
    <>
      <Tabs cur={stateUI.cur} setcur={stateUI.setCur} tabs={stateUI.tabsUIData} />
      <DispatchContent />
      {/* <DiffJSON  pre={effect.pre} next={effect.next} />
      <ChartPanel current={stateUI.cur} es={rules} JSONS={jsons} /> */}
    </>
  )
}
